const commonUrl = "https://test.kabubuda.xyz"

/**
 * @description POST
 * @param { String } url 
 * @param { Object } data 
 * @example 
 * this.$post(
 * 	'app/api', 
 * 	{name: 'yzp'}).then(res => {
 *     console.log('')}).catch(err => {console.log(err)})
 */
export function postRequest(url, data) {
	var promise = new Promise((resolve, reject) => {
		var that = this;
		var postData = data;
		this.$loading('...', true);
		uni.request({
			url: commonUrl + url,
			data: postData,
			method: "POST",
			header: {
				"content-type": "application/json",
				'token': uni.getStorageSync("token"),
				// Authorization: uni.getStorageSync("sessionId")

				//'Origin': 'https://test.kabubuda.xyz'

				'Origin': 'https://test.kabubuda.xyz',

			},
			success: function(res) {
				// console.log(res.header.token)
				uni.hideLoading();
				//
				if (res.statusCode == 200) {
					resolve(res.data); //  data .then() res
				} else {
					resolve(res.errMsg);
				}
			},
			error: function(e) {
				uni.hideLoading(); // 
				reject("");
			}
		});
	});
	return promise;
}

/**
 * @description GET
 * @param { String } url 
 * @param { Object } data 
 * @example 
 * this.$get(
 * 	'app/api', 
 * 	{name: 'yzp'}).then(res => {
 *     console.log('')}).catch(err => {console.log(err)})
 */
export function getRequest(url, data) {
	var promise = new Promise((resolve, reject) => {
		var that = this;
		var postData = data;
		// this.$loading('...', true);
		uni.request({
			url: commonUrl + url,
			data: postData,
			method: "GET",
			dataType: "json",
			header: {
				"content-type": "application/json",
				'token': uni.getStorageSync("token"),
				// Authorization: uni.getStorageSync("sessionId"),
				// 'Origin': 'http://47.119.155.5:8081'
				'Origin': 'https://test.kabubuda.xyz'
			},
			success: function(res) {
				uni.hideLoading();
				if (res.statusCode == 200) {
					resolve(res.data);
				} else {
					// code
					// then
					// resolve
					// 
					resolve(res.errMsg);
				}
			},
			error: function(e) {
				uni.hideLoading();
				reject("");
			}
		});
	});
	return promise;
}

function install(Vue) {
	Vue.prototype.$post = postRequest
	Vue.prototype.$get = getRequest
}

export default {
	install
}
