/**
 * @author YZP
 * @description , 
 * @param { String } title ,
 * @param { Number } duration ,1500
 * @param { String } icon ,none,: noneloadingsuccess
 * @param { Boolean } mask ,,,true
 * @example this.$toast('', 2000, 'success', true);
 */
export function popTip(title = '', duration = 1500, icon = 'none', mask = true) {
	uni.showToast({
		title: title,
		duration: duration,
		icon: icon,
		mask: mask
	})
}


/**
 * @author YZP
 * @description , 
 * @param { String } title ,
 * @param { Boolean } mask ,,,true
 * @example this.$loading('', true);
 */
export function loading(title = '', mask = true) {
	uni.showLoading({
		title: title,
		mask: mask
	})
}

function install(Vue) {
	Vue.prototype.$toast = popTip
	Vue.prototype.$loading = loading
}

export default { install }

